<template>
  <div class="index">
    <!-- 导航 -->
    <ao-nav></ao-nav>
    <!-- 轮播图 -->
    <div class="banner">
      <el-carousel
        :autoplay="true"
        :initial-index="index"
        height="540px"
        ref="carousel"
        @change="change"
      >
        <el-carousel-item v-for="src in bannerList" :key="src">
          <img :src="src" />
        </el-carousel-item>
      </el-carousel>
    </div>
    <!-- 虚拟货币信息 -->
    <div class="usdt">
      <div class="usdt-item" v-for="(item, index) in usdtList" :key="index">
        <div class="usdt-item-box">
          <div class="t">
            <div>
              <div class="title">{{ item.title }}</div>
              <div class="value">{{ item.value }}</div>
            </div>
            <div class="grow" :class="getClass(item.grow)">{{ item.grow + '%' }}</div>
          </div>
          <div class="b">
            <span class="ht">{{ item.ht }}</span>
            <span class="number">{{ item.number }}</span>
          </div>
        </div>
      </div>
    </div>
    <!-- 公告 -->
    <div class="notice">
      <div>
        <span
          v-for="(item, index) in noticeList"
          :key="index"
          @click="notice(item.id)"
        >{{ item.title }}</span>
      </div>
    </div>
    <!-- 公司特色1 -->
    <div class="feature">
      <h2>{{ featureTitle }}</h2>
      <p>{{ featureMsg }}</p>
      <div class="feature-list">
        <div class="feature-item" v-for="(item, index) in featureList" :key="index">
          <img :src="item.icon" />
          <h3>{{ item.title }}</h3>
          <p v-for="p in item.msg" :key="p">{{ p }}</p>
        </div>
      </div>
    </div>
    <!-- 公司特色2 -->
    <div class="feature2">
      <div class="feature2-bg">
        <h2>{{ featureTitle2 }}</h2>
        <p>{{ featureMsg2 }}</p>
        <div class="feature2-show">
          <div class="left">
            <div class="left-item">
              <p class="p0"></p>
              <span>IPhone</span>
            </div>
            <div class="left-item">
              <p class="p1"></p>
              <span>Android</span>
            </div>
            <div class="left-item">
              <p class="p2"></p>
              <span>Window</span>
            </div>
          </div>
          <div class="right">
            <img class="iconbg" :src="iconbg" />
            <img class="indexphone" :src="indexphone" />
          </div>
        </div>
      </div>
    </div>
    <!-- 注册 -->
    <div class="register">
      <h2>{{ $t('index.register_title') }}</h2>
      <div>
        <el-input v-model="value" :placeholder="$t('index.register_pl')">
          <template slot="suffix">
            <el-button type="primary" @click="register">{{ $t('index.register') }}</el-button>
          </template>
        </el-input>
      </div>
    </div>
    <!-- 脚部 -->
    <ao-footer></ao-footer>
  </div>
</template>

<script>
import AoNav from "@/components/AoNav";
import AoFooter from "@/components/AoFooter";
import icon1 from "@/assets/index-icon-1.png";
import icon2 from "@/assets/index-icon-2.png";
import icon3 from "@/assets/index-icon-3.png";
import iconbg from "@/assets/index-bg.png";
import indexphone from "@/assets/index-phone.png";
import { getGG, getCutttencyInfo, getBanner } from "@/api/index";
var index_timer = null;

export default {
  components: {
    AoNav,
    AoFooter
  },
  data() {
    return {
      iconbg: iconbg,
      indexphone: indexphone,
      bannerList: [],
      usdtList: [],
      noticeList: [],
      featureTitle: this.$t("index.featureTitle"),
      featureMsg: this.$t("index.featureMsg"),
      featureList: this.$t("index.featureList"),
      featureTitle2: this.$t("index.featureTitle2"),
      featureMsg2: this.$t("index.featureMsg2"),
      index: 0,
      value: ''
    };
  },
  mounted() {
    // index_timer && clearInterval(index_timer);
    getBanner().then(res => {
      this.bannerList = res
    })
    getGG().then(res => {
      this.noticeList = res;
    });
    this.getCutttencyInfo();
    // index_timer = setInterval(() => {
    //   this.getCutttencyInfo();
    // }, 2000);
    var banner = document.querySelector(".banner");
    banner.onmousedown = e => {
      var a1 = e.screenX;
      banner.onmousemove = e => {
        var a2 = e.screenX;
        if (a1 > a2) {
          banner.onmousemove = null;
          var index = this.index + 1;
          if (index > this.bannerList.length - 1) index = 0;
          this.$refs.carousel.setActiveItem(index);
        }
        if (a1 < a2) {
          banner.onmousemove = null;
          var index = this.index - 1;
          if (index < 0) index = this.bannerList.length - 1;
          this.$refs.carousel.setActiveItem(index);
        }
      };
    };
    __socket.emit("index", 'index');
    __socket.on("daymarket", msg => {
        if (typeof msg === 'string') msg = JSON.parse(msg)
        if (msg.type == "daymarket") {
          this.usdtList.forEach(o => {
            if (o.title == msg.symbol) {
              o.grow = msg.change
              o.number = msg.volume
              o.value = msg.now_price
            }
          })
        }
      });
  },
  destroyed() {
    index_timer && clearInterval(index_timer);
    console.log("123");
  },
  methods: {
    register() {
      this.$router.push({ path: '/register', query: { v: this.value } })
    },
    getCutttencyInfo() {
      getCutttencyInfo().then(res => {
        this.usdtList = res;
      });
    },
    notice(id) {
      this.$router.push({
        path: "/text",
        query: { id }
      });
    },
    getClass(grow) {
      return parseFloat(grow) < 0 ? "down" : "";
    },
    change(index) {
      this.index = index;
    }
  }
};
</script>

<style lang="scss">
.index {
  .banner,
  .el-carousel {
    width: 100%;
    img {
      width: 100%;
      height: 100%;
    }
  }
  .usdt {
    display: flex;
    border-top: 1px solid rgba(29, 50, 66, 0.7);
    background-color: #0a151e;
    color: #9eb5ca;
    // height: 7.5rem;
    .usdt-item {
      flex: 1;
      padding: 0.875rem 0;
      min-height: 7.5rem;
      cursor: pointer;
      &:hover {
        background: #122331;
      }
      .usdt-item-box {
        padding: 0 2.5rem;
        border-right: 1px solid rgba(29, 50, 66, 0.7);
      }
      .t {
        display: flex;
        justify-content: space-between;
      }
      .b {
        margin-top: 15px;
      }
      .title,
      .number {
        font-size: 0.875rem;
        color: #9eb5ca;
      }
      .grow {
        display: block;
        padding: 5px 1.25rem;
        border-radius: 5px;
        height: 1.875rem;
        line-height: 1.35;
        background: #0d2f32;
        color: #0d8551;
        &.down {
          color: #ef4034;
          background: #3b2128;
        }
      }
      .value {
        margin-top: 15px;
        font-size: 1.125rem;
        color: #9eb5ca;
      }
      .ht {
        font-size: 0.875rem;
        color: #477a90;
      }
    }
  }
  // 公告
  .notice {
    width: 100%;
    height: 3.25rem;
    border-top: 1px solid rgba(29, 50, 66, 0.7);
    margin-top: -1px;
    background-color: #f6f9fc;
    position: relative;
    & > div {
      margin: 0 30px;
      font-size: 12px;
      line-height: 3.25rem;
      height: 2.25rem;
      overflow: hidden;
      word-break: keep-all;
      text-align: center;
      span {
        display: inline-block;
        position: relative;
        padding: 0 2em 0 1.5em;
        color: #54748f;
        cursor: pointer;
        &:hover {
          color: #357ce1;
        }
        & + span {
          &::before {
            content: "/";
            position: absolute;
            left: -0.5em;
            color: #54748f;
          }
        }
      }
    }
  }
  // 特色 1
  .feature {
    background-color: #fff;
    padding: 5.625rem 0;
    text-align: center;
    color: #54748f;
    line-height: 2em;
    h2 {
      font-size: 1.75rem;
      margin-bottom: 0.625rem;
      color: #192544;
    }
    & > p {
      text-align: center;
      color: #54748f;
      line-height: 2em;
    }
    .feature-list {
      padding-top: 5rem;
      width: 70%;
      margin: 0 auto;
      display: flex;
      .feature-item {
        width: 33%;
        background: transparent none no-repeat top;
        h3 {
          font-size: 1.125rem;
          font-weight: 700;
          margin-bottom: 0.625rem;
          color: #192544;
        }
      }
    }
  }
  .feature2 {
    width: 100%;
    height: 710px;
    color: #9eb5ca;
    text-align: center;
    line-height: 2em;
    background-color: #fff;
    margin-top: 13rem;
    .feature2-bg {
      height: 700px;
      padding-top: 100px;
      background: url(data:image/svg+xml;base64,)
        no-repeat;
      background-size: cover;
      h2 {
        margin-bottom: 1.5rem;
        color: #fff;
        font-weight: 400;
        font-size: 1.75rem;
      }
      & > p {
        color: #9eb5ca;
      }
    }
    .feature2-show {
      display: flex;
      margin: 4.375rem 10% 0px;
      .left {
        height: 6.25rem;
        width: 50.625rem;
        .left-item {
          display: flex;
          margin: 0 9.375rem;
          color: #9eb5ca;
          padding: 2.5rem 0;
          line-height: 3rem;
          position: relative;
          cursor: pointer;
          p {
            height: 3rem;
            width: 3rem;
            margin: 0 0.625rem 0 0;
            vertical-align: top;
            border-radius: 50%;
            background: rgba(53, 124, 225, 0.1) none no-repeat 50%;
          }
          .p0 {
            background-image: url(data:image/svg+xml;base64,);
          }
          .p1 {
            background-image: url(data:image/svg+xml;base64,);
          }
          .p2 {
            background-image: url(data:image/svg+xml;base64,);
          }
          span {
          }
        }
      }
      .right {
        width: 32.375rem;
        position: relative;
        margin: 0 auto;
        .iconbg {
          width: 100%;
        }
        .indexphone {
          position: absolute;
          right: -3.75rem;
          bottom: -7rem;
          width: 7.625rem;
          height: 15.8125rem;
          background: #0a151e none no-repeat 50% / cover;
          box-shadow: -9px 12px 62px 0 rgba(36, 52, 69, 0.3);
          border-radius: 16.47px;
        }
      }
    }
  }
  // 注册
  .register {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 15rem;
    width: 100%;
    background: #fff;
    h2 {
      color: #232a4a;
      font-size: 1.375rem;
      line-height: 1.5rem;
      text-align: center;
    }
    & > div {
      margin: 2rem auto 0;
    }
    .el-input {
      width: 25rem;
    }
    .el-input__suffix {
      right: -5.625rem;
    }
  }
}
</style>



