<template>
  <div class="ao-footer">
    <div class="content">
      <dl>
        <dt>
          <img src="/static/image/logo.png" style="margin-top: 50px;max-width:400px;" />
        </dt>
      </dl>
      <dl v-for="(item, index) in dl" :key="index">
        <dt>{{ item.dt }}</dt>
        <dd v-for="(i,n) in item.dd" :key="n" @click="path(i.id)">{{ i.title }}</dd>
        <dd v-if="item.icon">
          <div class="img" v-for="(item, index) in icon" :key="index">
            <img v-for="src in item" :key="src" :src="src" />
          </div>
        </dd>
      </dl>
    </div>
    <div class="info">
      <p>{{ p }}</p>
    </div>
  </div>
</template>

<script>
import icon1 from "@/assets/index-footer-icon1.png";
import icon2 from "@/assets/index-footer-icon2.png";
import icon3 from "@/assets/index-footer-icon3.png";
import icon4 from "@/assets/index-footer-icon4.png";
import icon5 from "@/assets/index-footer-icon5.png";
import icon6 from "@/assets/index-footer-icon6.png";
import icon7 from "@/assets/index-footer-icon7.png";
import icon8 from "@/assets/index-footer-icon8.png";

export default {
  data() {
    return {
      icon: [[icon1, icon2, icon3, icon4], [icon5, icon6, icon7, icon8]],
      p: this.$t("footer.p"),
      dl: this.$t("footer.dd")
    };
  },
  methods: {
    path(id) {
      this.$router.push({
        path: "/text",
        query: { id }
      });
    }
  }
};
</script>

<style scoped lang="scss">
.ao-footer {
  width: 100%;
  background: #000;
  padding: 50px 0 0;
  text-align: center;
  .content {
    display: flex;
    width: 1200px;
    margin: 0 auto;
    justify-content: space-between;
    border-top: 1px solid #42f1eb;
    padding-top: 30px;
  }
  dl dt {
    font-size: 1rem;
    color: #fff;
    margin-bottom: 10px;
  }
  dl dd {
    font-size: 0.875rem;
    color: #a7b7c7;
    line-height: 24px;
    cursor: pointer;
    padding-top: 10px;
  }
  .img img {
    width: 2rem;
    height: 2rem;
    background: #303e63;
    border-radius: 50%;
    margin-right: 10px;
  }
  .info p {
    line-height: 4.375rem;
    border-top: 1px solid #303035;
    height: 4.375rem;
    color: #727b92;
    margin-top: 1.25rem;
    font-size: 0.75rem;
    text-align: center;
  }
}
</style>



